// About
.about {
    margin-top: $base-line-height * 2 + 36;
    margin-bottom: 106px;
    padding-bottom: $base-line-height * 4;

    font-size: $base-font-size + 2;
    background-color: rgba(#e8e8e8, 0.25);
    border-top: 1px solid #d7d7d7;
    border-bottom: 1px solid #d7d7d7;

    &__text-wrapper {
        padding-left: $base-line-height + 10;
    }

    &__title {
        margin: 0 auto;
        margin-bottom: $base-line-height * 2;
        margin-top: -36px;
    }

    &__small-title {
        margin-bottom: 15px;
        font-weight: 700;
        letter-spacing: 1px;
        color: #393939;
    }

    &__desc {
        margin-bottom: 10px;
        letter-spacing: 2px;
        color: rgba($color: #393939, $alpha: 0.8);
    }

    &__button {
        margin-top: 5px;
    }


    // Media

    @media all and (max-width: 991px) {
        & .row-fl {
            justify-content: center;
        }

        & .row-fl__col-5, 
        & .row-fl__col-7 {
            flex-basis: 90%;
        }

        &__img {
            margin-bottom: 40px;
        }

        &__text-wrapper {
            padding-left: 0;
        }

        &__desc {
            margin-bottom: 20px;
        }
    }
}
