// Advantages

.advantages {
    margin-top: $base-line-height * 2 + 36;

    font-size: $base-font-size + 2;

    border-top: 1px solid #d7d7d7;

    &__title {
        margin: 0 auto;
        margin-bottom: $base-line-height * 2;
        margin-top: -36px;
    }

    &__inner {
        position: relative;
        width: 231px;
        padding-bottom: 62px;
    }
    &__num-inner {
        display: inline-block;
        width: 40px;
        height: 40px;
        border: 1px solid #5b7d6e;
        transform: rotate(45deg);
    }
    &__num {
        display: inline-block;
        width: 40px;
        height: 40px;
        font-size: 32px;
        line-height: 40px;
        font-family: $lora-font;
        text-align: center;
        color: #5b7d6e;
        transform: rotate(-45deg);
    }
    &__desc {
        // max-width: 230px;
        padding-top: 15px;
        font-size: $base-font-size + 4;
        line-height: 27px;
        word-wrap: break-word;
        color: $base-menu-color;
    }

    @media (max-width: 768px) {
        &__inner {
            max-width: 30%;
        }
    }

    @media (max-width: 576px) {
        & .row-fl {
            justify-content: space-around;
        }

        &__title {
            padding: 20px 27px;
        }

        &__inner {
            min-width: 100%;
        }

        &__num-inner {
            position: absolute;
            top: 9px;
            left: 35px;
        }

        &__desc {
            padding-left: 100px;
            padding-right: 10px;
        }

    }
}
