// Hero
.hero {
    margin-top: $base-line-height;
    padding-bottom: $base-line-height * 2;
    color: $base-white-color;
    overflow: hidden;

    &__img {
        // width: 100%;
        vertical-align: bottom;
    }

    &__text-inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding-right: 35px;
        padding-left: 60px;
        height: 100%;
        // padding: 121px 44px 48px 55px;
        background-color: $base-green-color;
    }

    &__title {
        margin-bottom: $base-line-height * 2;
        font-size: 48px;
        font-weight: $base-font-weight;
        line-height: 64px;
        font-family: $lora-font;
        letter-spacing: 1.8px;
        text-transform: uppercase;
    }

    &__desc,
    &__footnote {
        margin-bottom: $base-line-height;
        line-height: 48px;
        font-weight: 300;
        font-family: $Montserrat-font;
    }

    &__desc {
        font-size: 36px;
    }

    &__footnote {
        font-size: 24px;
    }

    @media all and (max-width: 1199px) {
        &__title {
            font-size: 44px;
        }

        &__desc {
            font-size: 28px;
            line-height: 42px;
        }

        &__footnote {
            font-size: 20px;
            line-height: 32px;
        }
    }

    @media all and (max-width: 991px) {
        &__text-inner {
            padding-left: 45px;
        }

        &__title {
            margin-bottom: 32px;
            font-size: 30px;
            line-height: 48px;
        }

        &__desc {
            font-size: 22px;
            line-height: 30px;
        }

        &__footnote {
            font-size: 18px;
            line-height: 26px;
        }
    }

    @media all and (max-width: 767px) {
        &__text-inner {
            padding-left: 30px;
        }

        &__title {
            font-size: 22px;
            line-height: 34px;
        }

        &__desc {
            font-size: 18px;
            line-height: 26px;
        }

        &__footnote {
            font-size: 14px;
            line-height: 20px;
        }
    }

    @media all and (max-width: 575px) {
        & .row-fl__col-7,
        & .row-fl__col-5 {
            flex-basis: 100%;
        }

        &__text-inner {
            padding: 30px 15px;
        }

        &__title {
            margin-bottom: 35px;
            font-size: 36px;
        }

        &__desc {
            font-size: 26px;
            margin-bottom: 25px;
        }

        &__footnote {
            font-size: 16px;
            margin: 0;
        }
    }
}
