// Footer
.footer {
    padding-top: $base-line-height * 2;
    color: $base-white-color;
    background-color: $base-menu-color;

    &__title {
        margin-bottom: 15px;
        font-weight: 300;
        line-height: 24px;
        letter-spacing: 4px;
        text-transform: uppercase;
    }

    &__text {
        margin-bottom: 25px;
        font-size: 15px;
        line-height: 23px;
        font-weight: 300;
        font-family: $Montserrat-font;
        color: #e0e0e0;

        a {
            color: inherit;
            transition: $base-transition;

            &:hover {
                color: $second-green-color;
            }
        }

        &--feedback {
            margin-bottom: 20px;
            font-size: $base-font-size;
            color: $base-white-color;
        }
    }

    &__logo {
        position: relative;
        width: 150px;
        height: 150px;
        background-color: $base-white-color;
    }

    &__logo-img {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 105px;
        transform: translate(-50%, -50%);
    }

    &__button {
        display: block;
        min-width: 171px;
        color: $base-white-color;
        background-color: transparent;
        border: 1px solid $base-white-color;

        &:hover {
            color: $base-menu-color;
        }
    }

    &__copyright {
        margin-top: $base-line-height * 2;
        padding: 22px 0;
        font-size: $base-font-size - 2;
        font-family: $Montserrat-font;
        color: rgba($base-white-color, 0.7);
        border-top: 1px solid #c4c4c4;
    }

    &__copyright-text {
        margin-right: $base-line-height + 10;
        display: inline-block;

        a {
            color: inherit;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    & .green-link {
        color: $second-green-color;
    }

    &__social-inner {
        float: right;
    }

    &__social-vk {
        position: relative;
        margin-right: 20px;

        svg {
            position: absolute;
            top: 0;
            
            &:hover {
                filter: invert(35%) sepia(6%) saturate(2050%) hue-rotate(101deg) brightness(99%) contrast(85%);
            }
        }
    }

    &__social-insta {
        margin-left: 20px;

        svg {
            &:hover {
                &:hover {
                    filter: invert(35%) sepia(6%) saturate(2050%) hue-rotate(101deg) brightness(99%) contrast(85%);
                }
            }
        }
    }

    @media all and (max-width: 1199px) {
        & .row-fl__col-4,
        & .row-fl__col-2 {
            flex-basis: 25%;
        }
    }

    @media all and (max-width: 767px) {
        & .row-fl__col-3:first-child {
            display: none;
        }

        & .row-fl__col-4,
        & .row-fl__col-3,
        & .row-fl__col-2 {
            flex-basis: 33%;
        }

        @media all and (max-width: 767px) {
            & .row-fl__col-4,
            & .row-fl__col-3,
            & .row-fl__col-2 {
                flex-basis: 100%;
            }

            &__title,
            &__text,
            &__button {
                margin-left: 30px;
            }

            &__title {
                margin-bottom: 5px;
            }
        }

        @media all and (max-width: 575px) {
            &__button {
                min-width: 100%;
                margin: 0 auto;
            }
        }
    }
}

.footer-menu {
    &__item {
        margin-bottom: 15px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link {
        transition: $base-transition;

        &:hover {
            color: $second-green-color;
        }
    }

    @media all and (max-width: 767px) {
        & {
            margin-bottom: 30px;
        }
    }
}

.clearfix {
    clear: both;
}
