// Variables for layout
$container-size         : 1200px;
$container-indent       : 15px;
$container-full-indent  : 15px;

// Fonts 
$arimo-font-base         : "Arimo", sans-serif;
$lora-font               : "Lora", serif;
$Montserrat-font         : "Montserrat", sans-serif;

// Size
$base-font-size         : 16px;
$base-font-weight       : 400;
$base-line-height       : 20px;

//color
$base-white-color       : #ffffff;
$base-black-color       : #000000;
$base-green-color       : #446657;
$base-menu-color        : #342834;
$second-green-color     : #54ac84;

// Transitions
$base-transition        : all 0.15s linear;

