// Gallery
.gallery {
    margin-top: $base-line-height * 2 + 36;
    margin-bottom: 106px;
    border-top: 1px solid #d7d7d7;

    &__title {
        margin: 0 auto;
        margin-bottom: $base-line-height * 2;
        margin-top: -36px;
    }

    &__img {
        cursor: pointer;
        overflow: hidden;
        transition: $base-transition;

        &:hover {
            transform: scale(1.1, 1.1);
        }
    }
}