// Menu
.menu {
    padding: 26px 0;
    background-color: $base-menu-color;

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0 $base-line-height * 5;
    }

    &__item {
        position: relative;
    }

    &__link {
        padding: 27px 10px;
        font-size: $base-font-size + 2;
        color: $base-white-color;
        letter-spacing: 1.2px;
        text-transform: uppercase;
        transition: $base-transition;

        &:hover {
            color: $second-green-color;
        }
    }

    &__link--arrow {
        padding-right: 10px;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: -10px;
            display: block;
            width: 10px;
            height: 6px;
            background-image: url(../img/svg/arrow-down-white.svg);
            background-repeat: no-repeat;
            transform: translateY(-50%);
            transition: $base-transition;
        }

        &:hover {
            &::after {
                background-image: url(../img/svg/arrow-down-green.svg);
            }
        }
    }

    &__item:hover .sub-menu {
        display: block;
    }

    & .sub-menu {
        position: absolute;
        top: 47px;
        left: -30px;
        z-index: 500;
        display: none;
        min-width: max-content;
        border: 1px solid rgba($color: $base-menu-color, $alpha: 0.25);
        background-color: $base-white-color;

        &:first-child {
            margin-top: $base-line-height;
            position: relative;
        }

        &__item:hover .second-sub-menu {
            display: block;
        }

        &__link {
            display: inline-block;
            width: 100%;
            padding: 15px 30px 15px 25px;
            color: $base-menu-color;
            border-left: 5px solid $base-white-color;

            &:hover {
                background-color: #f9f9f9;
                border-left: 5px solid $base-menu-color;
            }
        }
    }

    & .second-sub-menu {
        position: absolute;
        top: -1px;
        right: -216px;
        display: none;
        background-color: $base-menu-color;

        &__item {
            padding: 20px 30px;
        }

        &__link {
            display: inline-block;
            color: $base-white-color;
            transition: $base-transition;

            &:hover {
                color: $second-green-color;
            }
        }
    }

    @media all and (max-width: 767px) {
        &__list {
            padding: 0;
        }
    }

    @media all and (max-width: 575px) {
        & {
            position: fixed;
            top: 0;
            z-index: 1000;
            width: 100%;
            padding: 16px 0;
        }

        &__list {
            display: none;
            padding-top: 20px;
            padding-left: 15px;
            flex-direction: column;
        }

        &__item {
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__link {
            padding-top: 0;
            padding-bottom: 0;
        }

        & .sub-menu {
            top: 30px;
            left: 10px;
        }

        & .second-sub-menu {
            position: static;
        }

        & .second-sub-menu__item {
            padding-left: 45px;
        }
    }
}

.burger-menu {
    display: none;
    font-size: 18px;
    color: #ffffff;
    letter-spacing: 1.2px;
    text-transform: uppercase;
    cursor: pointer;

    &__burger-line {
        position: relative;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 5px;
        display: inline-block;
        width: 20px;
        height: 2px;
        background-color: #e8e8e8;

        &::before,
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 20px;
            height: 2px;
            background-color: #e8e8e8;
        }

        &::before {
            top: -5px;
        }

        &::after {
            top: 5px;
        }
    }

    @media all and (max-width: 575px) {
        & {
            display: block;
        }
    }
}
