img {
    display: block;
    width: 100%;
    height: auto;
}

.slider-inner {
    position: relative;
}

.slider-arrow--left {
    position: absolute;
    z-index: 100;
    top: calc(50% - 25px);
    left: -25px;
    width: 50px;
    height: 50px;

    background: $base-white-color;
    border: 1px solid #5b7d6e;

    transform: rotate(45deg);
    transition: $base-transition;
    cursor: pointer;

    &:hover {
        background-color: #f9f9f9;
    }

    &::after {
        position: absolute;
        top: 18px;
        left: 15px;
        content: "";
        display: block;
        width: 19px;
        height: 12px;
        background: url(../img/svg/arrow-left.svg);
        background-repeat: no-repeat;
        transform: rotate(-45deg);
    }
}

.slider-arrow--right {
    position: absolute;
    z-index: 100;
    top: calc(50% - 25px);
    right: -24px;
    width: 50px;
    height: 50px;

    background: $base-white-color;
    border: 1px solid #5b7d6e;

    transform: rotate(45deg);
    transition: $base-transition;

    cursor: pointer;

    &:hover {
        background-color: #f9f9f9;
    }

    &::after {
        position: absolute;
        bottom: 18px;
        right: 15px;
        content: "";
        display: block;
        width: 20px;
        height: 12px;
        background: url(../img/svg/arrow-right.svg);
        background-repeat: no-repeat;
        transform: rotate(-45deg);
    }
}

.slider-arrow-catalog--left {
    position: absolute;
    z-index: 100;
    top: -102px;
    right: 30px;
    left: 0;
    width: 50px;
    height: 50px;

    background: $base-white-color;
    border: 1px solid #d7d7d7;
    transition: $base-transition;

    cursor: pointer;

    &:hover {
        background-color: #f9f9f9;
    }

    &::after {
        position: absolute;
        top: 18px;
        left: 15px;
        content: "";
        display: block;
        width: 19px;
        height: 12px;
        background: url(../img/svg/arrow-left.svg);
        background-repeat: no-repeat;
    }
}

.slider-arrow-catalog--right {
    position: absolute;
    z-index: 100;
    top: -102px;
    right: 0;
    width: 50px;
    height: 50px;

    background: $base-white-color;
    border: 1px solid #d7d7d7;
    transition: $base-transition;

    cursor: pointer;

    &:hover {
        background-color: #f9f9f9;
    }

    &::after {
        position: absolute;
        bottom: 18px;
        right: 15px;
        content: "";
        display: block;
        width: 20px;
        height: 12px;
        background: url(../img/svg/arrow-right.svg);
        background-repeat: no-repeat;
    }
}

.slider-dots {
    display: inline-block;
    width: 12px;
    height: 12px;
    border: 1px solid $base-white-color;
    cursor: pointer;

    &:hover {
        background-color: $base-white-color;
    }

    &--active {
        background-color: $base-white-color;
    }
}

.slider-dots__inner {
    position: absolute;
    right: 55px;
    bottom: 48px;
}

.slick-initialized .slick-slide {
    display: flex;
}

.slick-dots {
    position: absolute;
    right: 55px;
    bottom: 48px;

    li {
        display: inline-block;
        padding-right: 6px;

        button {
            width: 12px;
            height: 12px;

            font-size: 0;
            border: 1px solid $base-white-color;
            background: transparent;
            outline: none;
            cursor: pointer;
        }

        &.slick-active {
            button {
                background-color: $base-white-color;
            }
        }
    }
}

.slick-slide > div {
    margin: 0 auto;
}
