// Catalog-items
.catalog-items {
    position: relative;
    margin-top: $base-line-height * 2 + 36;
    margin-bottom: 106px;
    border-top: 1px solid #d7d7d7;

    &::before {
        content: "";
        position: absolute;
        top: -12px;
        left: 50px;
        z-index: 500;
        display: block;
        width: 15px;
        height: 25px;
        background-color: #ffffff;
        border-right: 1px solid #d7d7d7;
    }
    
    &::after {
        content: "";
        position: absolute;
        top: -12px;
        right: 50px;
        z-index: 500;
        display: block;
        width: 15px;
        height: 25px;
        background-color: #ffffff;
        border-left: 1px solid #d7d7d7;
    }

    &__title {
        margin: 0 auto;
        margin-bottom: 40px;
        margin-top: -36px;
    }

    @media all and (max-width: 575px) {
        &__title {
            padding: 20px 30px;
        }
    }
}

.catalog-item {
    width: 272px;
    text-transform: uppercase;
    border: 1px solid #d7d7d7;
    transition: $base-transition;

    &:hover {
        border: 1px solid #393939;
    }

    &__title {
        margin-top: $base-line-height;
        margin-bottom: $base-line-height * 2;
        font-size: $base-font-size;
        text-align: center;
        font-weight: $base-font-weight;
        letter-spacing: 2px;
        color: #393939;
    }

    &__link {
        color: inherit;

        &:hover {
            color: $base-green-color;
        }
    }
}
