// News
.news {
    margin-top: $base-line-height * 2;
    padding-bottom: $base-line-height * 4;

    font-size: $base-font-size + 2;
    background-color: rgba(#e8e8e8, 0.25);
    border-top: 1px solid #d7d7d7;

    &__title {
        margin: 0 auto;
        margin-bottom: $base-line-height * 2;
        margin-top: -36px;
    }

    &__button {
        display: block;
        min-width: 171px;
        margin: 0 auto;
    }

    @media all and (max-width: 1199px) {
        & .row-fl {
            justify-content: space-around;
        }
    }
    @media all and (max-width: 575px) {
        & .news-item {
            width: 290px;
        }
    }
}

// News-items
.news-items {
    margin-top: $base-line-height * 2 + 36;
    margin-bottom: 106px;

    border-top: 1px solid #d7d7d7;
    &__title {
        margin: 0 auto;
        margin-bottom: 40px;
        margin-top: -36px;
    }
}

.news-item {
    width: 352px;
    margin-bottom: $base-line-height * 2;
    border: 1px solid #d7d7d7;
    transition: $base-transition;

    &:hover {
        border: 1px solid #393939;
    }

    &__title {
        width: 250px;
        margin-top: $base-line-height;
        margin-bottom: $base-line-height / 2;
        padding-left: $base-line-height + 10px;
        font-size: $base-font-size;
        line-height: 23px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 1px;

        word-wrap: break-word;
        color: #393939;
    }

    &__link {
        color: inherit;

        &:hover {
            color: $base-green-color;
        }
    }

    &__desc {
        padding-left: $base-line-height + 10px;
        padding-bottom: 27px;
        font-size: $base-font-size - 2;
        line-height: 22px;
        font-family: $Montserrat-font;

        color: #0a0a0a;
    }
}