// Layout
.container {
    max-width: $container-size;
    width: 100%;
    padding-left: $container-indent;
    padding-right: $container-indent;
    margin: 0 auto;
}

.container-full {
    width: 100%;
    padding-left: $container-full-indent;
    padding-right: $container-full-indent;
    margin: 0 auto;
}

.row-fl {
    display: flex !important;
    flex-wrap: wrap;

    &__col-1 {
        flex-basis: 8.33333%;
    }

    &__col-2 {
        flex-basis: 16.66666%;
    }

    &__col-3 {
        flex-basis: 25%;
    }

    &__col-4 {
        flex-basis: 33.33333%;
    }

    &__col-5 {
        flex-basis: 41.66666%;
    }

    &__col-6 {
        flex-basis: 50%;
    }

    &__col-7 {
        flex-basis: 58.33333%;
    }

    &__col-8 {
        flex-basis: 66.66666%;
    }

    &__col-9 {
        flex-basis: 75%;
    }

    &__col-10 {
        flex-basis: 83.33333%;
    }

    &__col-11 {
        flex-basis: 91.66666%;
    }

    &__col-12 {
        flex-basis: 100%;
    }
}

.jc-sb {
    justify-content: space-between;
}

.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    gap: 30px 30px;
    grid-template-areas: "item-1 item-1 item-2 item-3 item-3" "item-1 item-1 item-6 item-7 item-8" "item-4 item-5 item-6 item-11 item-11" "item-9 item-9 item-10 item-11 item-11";
}

.item-1 {
    grid-area: item-1;
    overflow: hidden;
}

.item-2 {
    grid-area: item-2;
    overflow: hidden;
}

.item-3 {
    grid-area: item-3;
    overflow: hidden;
}

.item-4 {
    grid-area: item-4;
    overflow: hidden;
}

.item-5 {
    grid-area: item-5;
    overflow: hidden;
}

.item-6 {
    grid-area: item-6;
    overflow: hidden;
}

.item-7 {
    grid-area: item-7;
    overflow: hidden;
}

.item-8 {
    grid-area: item-8;
    overflow: hidden;
}

.item-9 {
    grid-area: item-9;
    overflow: hidden;
}

.item-10 {
    grid-area: item-10;
    overflow: hidden;
}

.item-11 {
    grid-area: item-11;
    overflow: hidden;
}

@media (max-width: 991px) {
    .grid-container {
        gap: 5px 10px;
    }
}
